import React, { Component  } from 'react'
import { Button, 
         Form, 
         InputGroup, 
         FormControl, 
         Container, 
         ButtonGroup, 
         Badge,
     
        } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'

class MainPage extends Component {

    constructor(props) {
        super(props)

        this.state = {
            user: '',
            comStr: '',
            commandsList: JSON.parse(window.localStorage.getItem('commandsList')) ,
        }
    }

    handleChangeUser = async event => {
        const user = event.target.value
        this.setState({ user })
    }
    handleChangeCommand = async event => {
        const comStr = event.target.value
        this.setState({ comStr })
    }

    handleSaveUserCommand = async () => {
        
        if (document.getElementById('user').value && document.getElementById('finalCommand').value ) {
            
            let { user, comStr, commandsList } = this.state
            const payload = { user, comStr }

            if ( commandsList !== null ) {
                commandsList.push(payload)    
            } else {
                commandsList = []
                commandsList.push(payload)
            }
            var commandsListJSON = JSON.stringify(commandsList)
            localStorage.setItem('commandsList', commandsListJSON)

            // await api.saveCommand(payload).then(res => {
            //     window.alert(`Команда сохранена`)
            //     this.setState({
            //         user: '',
            //         comStr: '',
            //     })
            //     this.handleReset()
            // })

        } else {
            window.alert('Сгенерируйте команду и укажите пользователя!')
        }
    }

    handleShowLastCommands = async () => {
        const user = this.state.user

        // await api.getCommandsByUser(user).then(commands => {
            //     commands = commands.data.data
            //     commands.reverse().forEach( function(item) {
                //     })             
                // }).catch(err => window.alert(`Пользователь не найден!`))
                
                // window.alert( JSON.parse(window.localStorage.getItem('commandsList')).length )
                
                document.getElementById('textarea').value = ""
                
                try {
                    if (!user) {
                        throw new Error("Укажите пользователя . . .")
                    }
                    let foundSome = false
                    
                    JSON.parse(window.localStorage.getItem('commandsList')).reverse().forEach(each => {
                        if (each.user === user) {
                            // eslint-disable-next-line
                    document.getElementById('textarea').value += each.comStr + "\n" + "\n"  
                    foundSome = true  
                }
            })
            
            if (!foundSome) {
                throw new Error("Такой пользователь не найден!");
            }
        } catch (error) {
            window.alert(error.message)
        }



    }
        

    handleGenerateCommand = async () =>  {
        let finalStringCommand = ""
        finalStringCommand += "Get-Childitem"
        let pathTyped = false

        if (document.getElementById('catalogPath').value) {
            finalStringCommand += " -Path " + document.getElementById('catalogPath').value + "\\*"
            pathTyped = 1
        }
        if (document.getElementById('include').value && pathTyped)  {
            finalStringCommand += " -Include " + document.getElementById('include').value
        } else if (document.getElementById('include').value && !pathTyped) {
            finalStringCommand += " " + document.getElementById('include').value
        }
        if (document.getElementById('exclude').value)  {
            finalStringCommand += " -Exclude " + document.getElementById('exclude').value
        }


        if (document.getElementById('recursiveCB').checked) {
            if (document.getElementById('depthCounter').value) {
                finalStringCommand += " -Depth " +  document.getElementById('depthCounter').value  
            } else {
                finalStringCommand += " -Recurse"
            }}
        if (document.getElementById('ignoreErrorsCB').checked) {
            finalStringCommand += " -ErrorAction SilentlyContinue"
        }
        if (document.getElementById('hiddenCB').checked) {
            finalStringCommand += " -Force"
        }
        if (document.getElementById('onlyfilesCB').checked) {
            finalStringCommand += " -File"
        }
        if (document.getElementById('onlydirsCB').checked) {
            finalStringCommand += " -Dir"
        }
        if (document.getElementById('sizegt').value) {
            finalStringCommand += " | Where-Object {$_.Length -gt " + 
                                document.getElementById('sizegt').value + "KB}"
        }
        if (document.getElementById('sizelt').value) {
            finalStringCommand += " | Where-Object {$_.Length -lt " + 
                                document.getElementById('sizelt').value + "KB}"
        }
        if (document.getElementById('datelt').value) {
            finalStringCommand += " | Where-Object {$_.lastwritetime -lt (get-date).adddays(-" + 
                                document.getElementById('datelt').value + ")}"
        }
        if (document.getElementById('dategt').value) {
            finalStringCommand += " | Where-Object {$_.lastwritetime -gt (get-date).adddays(-" + 
                                document.getElementById('dategt').value + ")}"
        }
        // послефалие
        if (document.getElementById('copyCB').checked) {
            if (document.getElementById('destinationPath').value) {
                finalStringCommand += " | Copy-Item -Destination " + 
                                    document.getElementById('destinationPath').value
            } else {
                finalStringCommand += " | Copy-Item -Destination Copied_Files " 
            }
            if (document.getElementById('recurseCopyCB').checked &&
            document.getElementById('recurseCopyCB').disabled === false ) {
                finalStringCommand += " -Recurse"
            }
        }
        if (document.getElementById('deleteCB').checked) {
                finalStringCommand += " | Remove-Item "

            if (document.getElementById('confirmCB').checked &&
            document.getElementById('confirmCB').disabled === false ) {
                finalStringCommand += " -Confirm"
            }
        }
        //финал
        document.getElementById('finalCommand').value = finalStringCommand
        // comStr = finalStringCommand
        // const user = document.getElementById('user').value
        // this.setState({ comStr })
        this.setState({
            comStr: finalStringCommand,
        })
    }
    handleReset() {
        window.location.reload()
    }
    handleCopy() {
        navigator.clipboard.writeText(document.getElementById('finalCommand').value)
    }    

    //..................//
    //Свитчи
    //..................//
    //RECURSE DEPTH
    handleRecursiveCB() {
        if (document.getElementById('recursiveCB').checked) {
            document.getElementById('depthCounter').disabled=false
        }
        else {
            document.getElementById('depthCounter').disabled=true
        }
    }
    //COPY DELETE
    handleCopyCB() {
        if (document.getElementById('copyCB').checked) {
            document.getElementById('deleteCB').checked=false
            document.getElementById('destinationPath').disabled=false
            document.getElementById('recurseCopyCB').disabled=false
            document.getElementById('confirmCB').disabled=true
        } else {
            document.getElementById('destinationPath').disabled=true
            document.getElementById('recurseCopyCB').disabled=true       
        }
    }
    handleDeleteCB() {
        if (document.getElementById('deleteCB').checked) {
            document.getElementById('copyCB').checked=false
            document.getElementById('destinationPath').disabled=true
            document.getElementById('recurseCopyCB').disabled=true  
            document.getElementById('confirmCB').disabled=false
        } else {
            document.getElementById('confirmCB').disabled=true     
        }
    }
    //FILES DIRS
    handleFilesCB() {
        if (document.getElementById('onlyfilesCB').checked) {
            document.getElementById('onlydirsCB').checked=false
            // document.getElementById('recurseCopyCB').checked=false

        }
    }
    handleDirsCB() {
        if (document.getElementById('onlydirsCB').checked) {
            document.getElementById('onlyfilesCB').checked=false
        }
        
    }
    //..................//
    //Свитчи
    //..................//
   

    render() {
        // const { user, comStr } = this.state 

        return (
            <Container>
                <Form>
                    <h1>
                        Команда <Badge variant="primary">Get-Child</Badge>
                    </h1>

                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Prepend>
                            <InputGroup.Text >Искать в каталогах</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                        placeholder="Не меняйте, если ищете в текущем каталоге"
                        id="catalogPath"
                        />
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Prepend>
                            <InputGroup.Text >Искать рекурсивно</InputGroup.Text>
                            <InputGroup.Checkbox id="recursiveCB" onChange={this.handleRecursiveCB} />
                            <InputGroup.Text >Глубина</InputGroup.Text>
                        </InputGroup.Prepend>   
                        <FormControl
                        id="depthCounter"
                        type="number"
                        disabled
                        placeholder="Не меняйте, если глубина не важна"
                        min="0"
                        step="1"
                        pattern="[0-9]+([,\.][0-9]+)?"
                        /> 
                        <InputGroup.Append>
                        <InputGroup.Text >Директорий</InputGroup.Text>
                        </InputGroup.Append>
                     </InputGroup>

                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Prepend>
                            <InputGroup.Text>Игнор ошибок</InputGroup.Text>
                        </InputGroup.Prepend>
                        <InputGroup.Append>
                            <InputGroup.Checkbox id="ignoreErrorsCB" />
                            <InputGroup.Text>Скрытые файлы</InputGroup.Text>
                            <InputGroup.Checkbox id="hiddenCB" />
                            <InputGroup.Text>Только файлы</InputGroup.Text>
                            <InputGroup.Checkbox id="onlyfilesCB" onClick={ this.handleFilesCB }/>
                            <InputGroup.Text>Только папки</InputGroup.Text>
                            <InputGroup.Checkbox id="onlydirsCB" onClick ={ this.handleDirsCB }/>
                        </InputGroup.Append>
                    </InputGroup>

                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Prepend>
                            <InputGroup.Text>Включить</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                        placeholder="Не меняйте, чтобы выделить всё"       // if -path empty, "-include" doent typed
                        id="include"
                        />
                        <InputGroup.Prepend>
                            <InputGroup.Text>Исключить</InputGroup.Text>
                        </InputGroup.Prepend>  
                        <FormControl
                        placeholder="*.ogg, *.wma"
                        id="exclude"
                        />           
                    </InputGroup>

                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Prepend>
                            <InputGroup.Text >Файлы больше</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                        placeholder="Размер"
                        id="sizegt"
                        type="number"
                        min="0"
                        />
                        <InputGroup.Append>
                            <InputGroup.Text >Килобайт</InputGroup.Text>
                        </InputGroup.Append>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Prepend>
                            <InputGroup.Text >Файлы меньше</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                        placeholder="Размер"
                        id="sizelt"
                        type="number"
                        min="0"
                        />
                        <InputGroup.Append>
                            <InputGroup.Text >Килобайт</InputGroup.Text>
                        </InputGroup.Append>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Prepend>
                            <InputGroup.Text >Файлы старше</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                        placeholder="дни"
                        id="datelt"
                        type="number"
                        min="0"
                        />
                        <InputGroup.Append>
                            <InputGroup.Text >дней</InputGroup.Text>
                        </InputGroup.Append>
                    </InputGroup>
                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Prepend>  
                            <InputGroup.Text >Файлы младше</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                        placeholder="дни"
                        id="dategt"
                        type="number"
                        min="0"
                        />
                        <InputGroup.Append>
                            <InputGroup.Text >дней</InputGroup.Text>
                        </InputGroup.Append>
                    </InputGroup>
                    
                    <h4>
                        Действия с выбранными объектами
                    </h4>

                    <InputGroup size="sm" className="mb-3">
                        <InputGroup.Prepend>
                            <InputGroup.Text>Копировать</InputGroup.Text>
                            <InputGroup.Checkbox id="copyCB" onClick={ this.handleCopyCB }/>
                        </InputGroup.Prepend>
                        <FormControl
                            // type="text"
                            as="input"
                            id="destinationPath"
                            placeholder="Каталог назначения"
                            disabled
                            />  
                        <InputGroup.Append>
                            <InputGroup.Text>Рекурсивно</InputGroup.Text>
                            <InputGroup.Checkbox id="recurseCopyCB" disabled onClick={ this.handleRecurseCopyCB }/>
                        </InputGroup.Append>
                    </InputGroup>

                    <InputGroup size="sm" className="mb-3">
                    <InputGroup.Prepend>
                            <InputGroup.Text>Удалить</InputGroup.Text>
                            <InputGroup.Checkbox id="deleteCB" onClick={ this.handleDeleteCB }/>
                        </InputGroup.Prepend>
                        <InputGroup.Append>
                            <InputGroup.Text>С подтвеждением</InputGroup.Text>
                            <InputGroup.Checkbox  id="confirmCB" disabled/>
                        </InputGroup.Append>
                    </InputGroup>

                    <br/>

                    <InputGroup className="mb-3">
                        <InputGroup.Prepend>
                            <InputGroup.Text>Итоговая команда</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                        readOnly 
                        id="finalCommand"
                        onChange={this.handleChangeCommand}
                        />
                        <InputGroup.Append>
                            <Button variant="outline-primary" onClick={  this.handleCopy }>Копировать</Button>
                        </InputGroup.Append>                
                    </InputGroup>

                    <ButtonGroup aria-label="Basic example">
                        <Button variant="primary" onClick={this.handleGenerateCommand}>Сгенерировать</Button>
                        <Button variant="danger" onClick={this.handleReset}>Сбросить</Button>
                    <Button variant="warning" onClick={this.handleSaveUserCommand}>Сохранить</Button>
                        <Button variant="info" onClick={ this.handleShowLastCommands } >Показать</Button>
                        <InputGroup.Append>
                        <FormControl
                            id="user"
                            placeholder="Имя пользователя"
                            onChange={this.handleChangeUser}
                        />
                        </InputGroup.Append>
                    </ButtonGroup>

                    <Form.Group>
                        <Form.Label><h6>Последние команды</h6></Form.Label>
                        <Form.Control 
                        id="textarea"
                        as="textarea" 
                        rows={10} 
                        value=""
                        placeholder="Сохранённые команды"
                        readOnly
                        onChange={this.handleCopy}
                        />
                    </Form.Group>

                </Form>
           
            </Container>
        )
    }

}

export default MainPage;